import { motion } from "framer-motion";

function AdminElementContainer({ children, className, duration = 0.3 }) {

    return (
        <>
            <motion.div
                className={className}
                initial={{ opacity: 0, x: 500 }} // Bt u m + lch tri
                animate={{ opacity: 1, x: 0 }}  // Khi hin th, hin dn + v ng v tr
                exit={{ opacity: 0, x: 500 }}    // Khi ri trang, m dn + lch phi
                transition={{ duration: duration }}  // Tc  chuyn ng
            >
                {children}
            </motion.div>
        </>
    )
}
export default AdminElementContainer;